.scoreboard {
    height: calc(100% - 150px);
    background: #606d7d;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

.navigation {
    height: 100%;
    width: 90px;
    background: #2d3945;
    border: none;
    border-right: 2px solid black;
    border-left: 2px solid black;
    font-family: 'VT323-Regular';
    font-size: 80px;
    color: white;
}

.score {
    height: 100%;
    width: 100%;
    margin: 0 20px;
    border-collapse: collapse;
    text-align: center;
    font-family: 'PressStart-Regular';
    font-size: 18px;
}

tr {
    height: 10%;
}

tr:first-child td {
    border-bottom: 4px solid white;
}

tr:last-child td {
    border-bottom: none;
}

td:first-child {
    color: white;
    padding: 10px 20px;
    text-align: left;
    width: 30%;
}

td:last-child {
    border-right: none;
    width: 15%;
}

td {
    border-style: solid;
    border-width: 0 2px 2px 0;
    border-color: white;
}



