.timer {
    width: 190px;
    height: 50px;
    display: -ms-flexbox;
    display: flex;
    margin: 10px;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    background: #2d3945;
    border: 2px solid black;
    border-radius: 5px;
    text-align: center;

}

.time {
    padding: 0;
    margin: 0;
    float: left;

    font-family: 'VT323-Regular';
    font-size: 60px;
    line-height: 50px;
    color: white;
}

.divider {
    width: 20px;
    height: 100%;
    padding: 0;
    margin: 0;
    float: left;

    font-family: 'VT323-Regular';
    font-size: 50px;
    line-height: 50px;
    color: white;
}


