.profile {
    width: 100%;
    height: 200px;
    margin-top: 20px;
    position: relative;
    font-family: VT323-Regular;
    font-size: 32px;
    color: #2d3945;
    text-align: center;
    display:grid;
    justify-items: center;
    -ms-flex-line-pack: start;
        align-content: flex-start;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr 2fr;
    grid-template-areas: 'name_lbl email_lbl' 'name email' 'start-button start-button';
    border-top: 2px solid#2d3945;
}

.profile label {
    width: 100%;
    position: absolute;
    padding-top: 10px;
}

.profile label:nth-child(1) {
    grid-area: name_lbl;
}

.profile label:nth-child(3) {
    grid-area: email_lbl;
}

.profile input {
    width: 62%;
    height: 64%;
    border: 2px solid #fefe00;
    color: darkgray;
    background: #2d3945;
    font-family: 'VT323-Regular';
    font-size: 20px;
    padding: 0 18px;
}

.profile input:nth-child(2) {
    grid-area: name;
}


.profile input:nth-child(4) {
    grid-area: email;
}

.profile a {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    grid-area: start-button;
    height: 40px;
    width: 140px;
    margin: 20px;
    border: 2px solid #fefe00;
    color: darkgray;
    background: #2d3945;
    font-family: 'VT323-Regular';
    font-size: 28px;
    text-decoration: none;
}


