@font-face {
    font-family: 'PressStart-Regular';
    src: local('PressStart-Regular') url('../../fonts/pressstart2p-regular-webfont.woff2') format('woff2'),
    url('../../fonts/pressstart2p-regular-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'VT323-Regular';
    src: local('VT323-Regular') url('../../fonts/vt323-regular-webfont.woff2') format('woff2'),
    url('../../fonts/vt323-regular-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

.card-board {
    height: calc(100% - 150px);
    width: 100%;
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: start;
        justify-content: flex-start;
    background: #606d7d;
}

.card-board .container {
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: start;
        justify-content: flex-start;
}

.card-board h5 {
    margin: 50px 0;
    font-family: 'PressStart-Regular';
    color: darkgray;
    font-size: 60px;
}

.card-board h4 {
    margin: 50px 0;
    font-family: 'PressStart-Regular';
    color: white;
    font-size: 60px;
}

.card-board a {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    height: 40px;
    width: 140px;
    border: 2px solid #fefe00;
    color: darkgray;
    background: #2d3945;
    font-family: 'VT323-Regular';
    font-size: 28px;
    text-decoration: none;
}


