* {
    padding: 0;
    margin: 0;
}

header {
    grid-area: header;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: start;
        align-items: flex-start;
    -ms-flex-pack: center;
        justify-content: center;
    height: 86px;
    width: 100%;
    background: #8e908f;
    border-bottom: 2px solid black;
    z-index: 2;
}

.header_logo {
    height: 100%;
    width: 350px;
    position: relative;
}

.header_logo a {
    width: 100%;
    height: 100%;
    text-decoration: none;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.header_logo h1 {
    font-family: 'PressStart-Regular';
    font-size: 26px;
    text-align: center;
    color: #fefe00;
    line-height: 110%;
}

nav {
    height: 100%;
    width: 850px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
    -ms-flex-align: center;
        align-items: center;
}

nav select {
    height: 40px;
    width: 200px;
    font-family: 'VT323-Regular';
    font-size: 24px;
    background-color: #8e908f;
    border: 2px solid #fefe00;
    color: white;
    text-align: center;
    text-decoration: none;
    margin: 0 10px;
}

nav button {
    height: 40px;
    width: 200px;
    font-family: 'VT323-Regular';
    font-size: 32px;
    background-color: #8e908f;
    border: 2px solid #fefe00;
    color: white;
    text-align: center;
    text-decoration: none;
    margin: 0 10px;
}

nav a {
    text-decoration: none;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    grid-area: start-button;
    height: 40px;
    width: 140px;
    margin: 20px;
    border: 2px solid #fefe00;
    color: darkgray;
    background: #2d3945;
    font-family: 'VT323-Regular';
    font-size: 28px;
    text-decoration: none;
}


