.rules {
    height: 60%;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.rules h2 {
    font-family: 'PressStart-Regular';
    font-size: 26px;
    text-align: center;
    color: #fefe00;
    margin: 20px 0;
}

.rules p {
    font-family: 'VT323-Regular';
    font-size: 32px;
    color: white;
    padding: 0;
    margin: 5px 0;
    text-align: center;
}

.rules ol {
    font-family: 'VT323-Regular';
    font-size: 32px;
    color: white;
    text-align: left;
    width: 60%;
    margin: 0 auto;
}


