* {
    padding: 0;
    margin: 0;
}

footer {
    grid-area: footer;
    position: relative;
    height: 60px;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    background-color: #2d3945;
    border-top: 2px solid black;
}

.info {
    height: 100%;
    width: 50%;
    padding: 0;
    margin: 0;
    font-family: 'VT323-Regular';
    font-size: 24px;
    color: white;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
}

.info p {
    position: relative;
    margin: 0;
    padding: 4px 10px;
}

.social {
    height: 100%;
    width: 50%;
    padding: 0 20px;
    margin: 10px 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: end;
        align-items: flex-end;
    -ms-flex-pack: end;
        justify-content: flex-end;
    font-family: 'VT323-Regular';
    font-size: 16px;
    color: white;
    text-align: right;
}

.social a {
    display: inline-block;
    position: relative;
    width: 16px;
    height: 16px;
    margin-right: 10px;
    margin-bottom: 16px;
}

.social a:first-child {
    background: url('../../images/social_ico.gif');
}

.social a:nth-child(2) {
    background: url('../../images/social_ico.gif');
    background-position: -16px 0;
}

.social a:last-child {
    background: url('../../images/social_ico.gif');
    background-position: 16px 0;
}


